<template>
  <div>
    <div class="dentaku">
      <div class="display">{{current | trimZero}}</div>
      <div @click="clear" class="btn">AC</div>
      <div @click="sign" class="btn">+/-</div>
      <div @click="percent" class="btn">%</div>
      <div class="btn">÷</div>
      <div @click="append(7)" class="btn">7</div>
      <div @click="append(8)" class="btn">8</div>
      <div @click="append(9)" class="btn">9</div>
      <div class="btn">×</div>
      <div @click="append(4)" class="btn">4</div>
      <div @click="append(5)" class="btn">5</div>
      <div @click="append(6)" class="btn">6</div>
      <div class="btn">-</div>
      <div @click="append(1)" class="btn">1</div>
      <div @click="append(2)" class="btn">2</div>
      <div @click="append(3)" class="btn">3</div>
      <div class="btn">+</div>
      <div @click="append(0)" class="zero btn">0</div>
      <div @click="dot" class="btn">.</div>
      <div class="btn">=</div>
    </div>
    {{$data}}
  </div>
</template>

<script>
export default {
	name: 'Dentaku',
	data() {
		return { current: '9' }
	},
	methods: {
		clear: function() {
			this.current = '0'
		},
		sign: function() {
			this.current = String(-Number(this.current))
		},
		percent: function() {
			this.current = String(Number(this.current) / 100)
		},
		append: function(num) {
			this.current += String(num)
		},
		dot: function() {
			if (this.current.indexOf('.') === -1) {
				this.current += '.'
			}
		}
	},
	filters: {
		trimZero: function(value) {
      if(value.endsWith('.')){
        return String(Number(value))+"."
      }
      else{
        return String(Number(value))
      }
		}
	}
}
</script>

